/* UNSUBSCRIBE / SUBSCRIBE STYLES
-----------------------*/

body {
    font-family: Arial, Helvetica, sans-serif;

    padding-top: 65px;

    text-align: center;

    background: #f2f2f2;
}

a {
    color: #185787;
    &:hover {
        color: #185787;
    }
}

p {
    margin: 0;
    padding: 0 0 13px;
}

.container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 34px 0 24px;

    border: 1px solid #ddd;
    border-radius: 6px;
    background: #fff;
}

.instructions,
.title {
    padding: 0 39px;

    text-align: left;
}

.instructions {
    h2,
    h3 {
        font-size: 30px;
        font-weight: 400;

        margin: 0 0 13px;
        padding: 0;

        letter-spacing: 0;

        color: #262626;
    }
}

.title {
    h2,
    h3 {
        font-size: 30px;
        font-weight: 400;

        margin: 0 0 13px;
        padding: 0;

        letter-spacing: 0;

        color: #262626;
    }
}

.instructions h3,
.title h3 {
    font-size: 22px;
}

.instructions p,
.title p {
    font-size: 16px;
    font-weight: 400;
    line-height: 25px;

    margin: 0;
    padding: 0 0 21px;

    color: #3e434a;
}

.cta {
    margin: 0 0 34px;
    padding: 20px 39px;

    text-align: left;
}

.error {
    font-size: 16px;

    margin: 0 0 34px;
    padding: 20px 39px;

    text-align: left;

    color: #930;
    border-top: 1px solid #e9d5d1;
    border-bottom: 1px solid #e9d5d1;
    background: #f8f2f1;
}

.cta {
    font-size: 14px;

    color: #1f1f1f;
    border-top: 1px solid #dae3ea;
    border-bottom: 1px solid #dae3ea;
    background: #f1fafe;
    .action {
        line-height: 1.8;

        min-height: 32px;
        padding: 0 0 0 40px;
    }
    .icon-warning {
        background: url(/img/icon_warning.gif) left top no-repeat;
    }
    .icon-mistake {
        background: url(/img/icon_mistake.gif) 5px top no-repeat;
    }
    .icon-added {
        background: url(/img/icon_added.gif) 5px top no-repeat;
    }
}

@media (max-width: 580px) {
    body {
        padding: 1px;
    }
    .container {
        padding: 12px 0;
    }
    .instructions,
    .title {
        padding: 0 10px;
    }
    .instructions h2,
    .title h2 {
        font-size: 22px;
    }
    .instructions h3,
    .title h3 {
        font-size: 18px;
    }
    .cta,
    .error {
        padding: 10px 7px;
    }
}
